import { ref, reactive, computed, toRefs, readonly } from 'vue'
import { getIpLocation, refreshIpLocation, type IpLocationResponse } from '../utils/api/location'
import { useMessage } from './useMessage'
import { useUserStore } from '../stores/user'

// IPÂÆö‰ΩçÁä∂ÊÄÅÁ±ªÂûã
interface LocationState {
  loading: boolean
  data: IpLocationResponse | null
  error: string | null
  lastUpdated: Date | null
}

export const useLocation = () => {
  const { success, error, warning } = useMessage()
  const userStore = useUserStore()
  
  // ÂìçÂ∫îÂºèÁä∂ÊÄÅ
  const state = reactive<LocationState>({
    loading: false,
    data: null,
    error: null,
    lastUpdated: null
  })
  
  // ÊòØÂê¶Ëá™Âä®Ëé∑ÂèñËøáÂÆö‰Ωç
  const hasAutoFetched = ref(false)
  
  /**
   * Ëé∑ÂèñIPÂÆö‰Ωç‰ø°ÊÅØ
   * @param showNotification ÊòØÂê¶ÊòæÁ§∫ÈÄöÁü•Ê∂àÊÅØ
   * @param forceRefresh ÊòØÂê¶Âº∫Âà∂Âà∑Êñ∞ÔºàÂøΩÁï•ÁºìÂ≠òÔºâ
   */
  const fetchLocation = async (showNotification = false, forceRefresh = false) => {
    if (state.loading) return state.data
    
    state.loading = true
    state.error = null
    
    try {
      // ÂâçÁ´ØÊ£ÄÊü•ÔºöÂè™ÊúâÁôªÂΩïÁî®Êà∑ÊâçË∞ÉÁî®APIËøõË°åÊï∞ÊçÆÂ∫ìÊõ¥Êñ∞
      const isLoggedIn = userStore.isLoggedIn
      
      if (showNotification && !isLoggedIn) {
        warning('ËØ∑ÂÖàÁôªÂΩï‰ª•ÂêØÁî®ÂÆåÊï¥ÁöÑIPÂÆö‰ΩçÂäüËÉΩ')
      }
      
      const response = forceRefresh 
        ? await refreshIpLocation() 
        : await getIpLocation()
      
      if (response.success && response.data) {
        state.data = response.data
        state.lastUpdated = new Date()
        
        if (showNotification) {
          const { location, user } = response.data
          if (location.error) {
            warning('IPÂÆö‰ΩçÂ§±Ë¥•: ' + location.error)
          } else {
            const locationText = `${location.province || 'Êú™Áü•Âú∞Âå∫'} ${location.city || ''}`
            const cacheText = response.data.cached ? '(ÁºìÂ≠ò)' : ''
            
            if (isLoggedIn && user?.locationUpdated) {
              success(`ÂÆö‰ΩçÊàêÂäü: ${locationText}${cacheText}ÔºåÁôªÂΩï‰ø°ÊÅØÂ∑≤Êõ¥Êñ∞`)
            } else if (isLoggedIn && !user?.locationUpdated) {
              warning(`ÂÆö‰ΩçÊàêÂäü: ${locationText}${cacheText}Ôºå‰ΩÜÁôªÂΩï‰ø°ÊÅØÊú™Êõ¥Êñ∞`)
            } else {
              success(`ÂÆö‰ΩçÊàêÂäü: ${locationText}${cacheText}ÔºàÊú™ÁôªÂΩïÁä∂ÊÄÅÔºâ`)
            }
          }
        }
      } else {
        state.error = response.message || 'Ëé∑ÂèñÂÆö‰Ωç‰ø°ÊÅØÂ§±Ë¥•'
        if (showNotification) {
          error(state.error)
        }
      }
      
      return state.data
    } catch (err: any) {
      state.error = err.message || 'ÁΩëÁªúÈîôËØØ'
      if (showNotification) {
        error('Ëé∑ÂèñÂÆö‰Ωç‰ø°ÊÅØÂ§±Ë¥•')
      }
      console.error('IPÂÆö‰ΩçÈîôËØØ:', err)
      return null
    } finally {
      state.loading = false
    }
  }
  
  /**
   * Ëá™Âä®Ëé∑ÂèñÂÆö‰Ωç‰ø°ÊÅØÔºà‰ªÖÂú®È¶ñÊ¨°Ë∞ÉÁî®Êó∂ÊâßË°åÔºâ
   * ÈÄÇÁî®‰∫éÂú®Â∫îÁî®ÂêØÂä®Êó∂Ëá™Âä®Ëé∑ÂèñÂÆö‰Ωç
   */
  const autoFetchLocation = async () => {
    if (hasAutoFetched.value) return state.data
    
    hasAutoFetched.value = true
    
    // Á°Æ‰øùÁî®Êà∑storeÂ∑≤ÂàùÂßãÂåñ
    userStore.initializeAuth()
    
    return await fetchLocation(false, false)
  }
  
  /**
   * Âº∫Âà∂Âà∑Êñ∞ÂÆö‰Ωç‰ø°ÊÅØ
   */
  const refreshLocation = async (showNotification = true) => {
    return await fetchLocation(showNotification, true)
  }
  
  /**
   * Ëé∑ÂèñÂΩìÂâçÂÆö‰ΩçÁöÑÊñáÊú¨ÊèèËø∞
   */
  const getLocationText = () => {
    if (!state.data?.location) return 'Êú™Áü•‰ΩçÁΩÆ'
    
    const { location } = state.data
    if (location.error) return 'ÂÆö‰ΩçÂ§±Ë¥•'
    
    return `${location.province || 'Êú™Áü•'} ${location.city || ''}`
  }
  
  /**
   * Ê£ÄÊü•ÊòØÂê¶ÊúâÂÆö‰ΩçÊï∞ÊçÆ
   */
  const hasLocation = computed(() => {
    return !!(state.data?.location && !state.data.location.error)
  })
  
  /**
   * Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶Â∑≤ÁôªÂΩï‰∏îÁôªÂΩïIPÂ∑≤Êõ¥Êñ∞
   */
  const isUserLocationUpdated = computed(() => {
    return !!(state.data?.user?.locationUpdated)
  })
  
  /**
   * Ëé∑ÂèñIPÂú∞ÂùÄ
   */
  const currentIp = computed(() => {
    return state.data?.ip || 'Êú™Áü•'
  })
  
  /**
   * Ê£ÄÊü•Áî®Êà∑ÁôªÂΩïÁä∂ÊÄÅÔºàÂâçÁ´ØÂà§Êñ≠Ôºâ
   */
  const isUserLoggedIn = computed(() => {
    return userStore.isLoggedIn
  })
  
  return {
    // Áä∂ÊÄÅ
    ...toRefs(state),
    hasAutoFetched: readonly(hasAutoFetched),
    
    // ÊñπÊ≥ï
    fetchLocation,
    autoFetchLocation,
    refreshLocation,
    getLocationText,
    
    // ËÆ°ÁÆóÂ±ûÊÄß
    hasLocation,
    isUserLocationUpdated,
    currentIp,
    isUserLoggedIn,
    
    // ÂéüÂßãÁä∂ÊÄÅÔºàÁî®‰∫éÈúÄË¶ÅÂÆåÊï¥Áä∂ÊÄÅÁöÑÂú∫ÊôØÔºâ
    locationState: readonly(state)
  }
} 