import { apiClient } from '../apiClient'
import type { ApiResponse } from '../../types/api'

// IPÂÆö‰ΩçÁõ∏ÂÖ≥Á±ªÂûã
export interface IpLocationResponse {
  ip: string
  location: {
    ip: string
    province?: string
    city?: string
    adcode?: string
    rectangle?: string
    response_time: string
    error?: string
    error_code?: string
    error_details?: string
  }
  cached: boolean
  user?: {
    id: number
    locationUpdated: boolean  // Áî®Êà∑ÁöÑÁôªÂΩïIPÂíåÂÆö‰Ωç‰ø°ÊÅØÊòØÂê¶Â∑≤Êõ¥Êñ∞
  } | null
}

// IPÂÆö‰ΩçAPIÁ±ª
export class LocationApi {
  /**
   * Ëé∑ÂèñÂΩìÂâçÁî®Êà∑ÁöÑIPÂÆö‰Ωç‰ø°ÊÅØ
   * Â¶ÇÊûúÁî®Êà∑Â∑≤ÁôªÂΩïÔºå‰ºöËá™Âä®Êõ¥Êñ∞Áî®Êà∑ÁöÑloginIpÂ≠óÊÆµ
   */
  static async getIpLocation(): Promise<ApiResponse<IpLocationResponse>> {
    try {
      const response = await apiClient.get<IpLocationResponse>('/amap/ip/')
      
      // Â¶ÇÊûúÊàêÂäüËé∑ÂèñÂÆö‰Ωç‰ø°ÊÅØÔºåÂèØ‰ª•Âú®ËøôÈáåÂÅö‰∏Ä‰∫õÂ§ÑÁêÜ
      if (response.success && response.data) {
        const { location, user } = response.data
        
        // ËæìÂá∫ÂÆö‰Ωç‰ø°ÊÅØÂà∞ÊéßÂà∂Âè∞ÔºàÂºÄÂèëÁéØÂ¢ÉÔºâ
        if (process.env.NODE_ENV === 'development') {
          console.log('üåç IPÂÆö‰Ωç‰ø°ÊÅØ:', {
            ip: location.ip,
            location: location.error ? 'ÂÆö‰ΩçÂ§±Ë¥•' : `${location.province || 'Êú™Áü•'} ${location.city || ''}`,
            cached: response.data.cached,
            userUpdated: user?.locationUpdated || false
          })
        }
      }
      
      return response
    } catch (error) {
      console.error('Ëé∑ÂèñIPÂÆö‰ΩçÂ§±Ë¥•:', error)
      throw error
    }
  }
  
  /**
   * Âº∫Âà∂Âà∑Êñ∞IPÂÆö‰Ωç‰ø°ÊÅØÔºàÂøΩÁï•ÁºìÂ≠òÔºâ
   * Ê≥®ÊÑèÔºöËøô‰ºöÁõ¥Êé•Ë∞ÉÁî®È´òÂæ∑Âú∞ÂõæAPIÔºåËØ∑Ë∞®ÊÖé‰ΩøÁî®
   */
  static async refreshIpLocation(): Promise<ApiResponse<IpLocationResponse>> {
    try {
      // ÂèØ‰ª•Ê∑ªÂä†Êó∂Èó¥Êà≥ÂèÇÊï∞Êù•ÈÅøÂÖçÁºìÂ≠ò
      const timestamp = new Date().getTime()
      const response = await apiClient.get<IpLocationResponse>(`/amap/ip/?t=${timestamp}`)
      
      if (process.env.NODE_ENV === 'development') {
        console.log('üîÑ Âº∫Âà∂Âà∑Êñ∞IPÂÆö‰Ωç‰ø°ÊÅØ')
      }
      
      return response
    } catch (error) {
      console.error('Âà∑Êñ∞IPÂÆö‰ΩçÂ§±Ë¥•:', error)
      throw error
    }
  }
}

// ÂØºÂá∫Êñπ‰æø‰ΩøÁî®ÁöÑÂáΩÊï∞
export const getIpLocation = LocationApi.getIpLocation
export const refreshIpLocation = LocationApi.refreshIpLocation

export default LocationApi 