import { createDiscreteApi } from 'naive-ui'

// ÂàõÂª∫Áã¨Á´ãÁöÑÊ∂àÊÅØAPIÂÆû‰æã
const { message, notification, dialog, loadingBar } = createDiscreteApi([
  'message',
  'notification', 
  'dialog',
  'loadingBar'
])

export const useMessage = () => {
  // ÊàêÂäüÊ∂àÊÅØ
  const success = (content: string, duration = 3000) => {
    message.success(content, { duration })
  }

  // ÈîôËØØÊ∂àÊÅØ  
  const error = (content: string, duration = 4000) => {
    message.error(content, { duration })
  }

  // Ë≠¶ÂëäÊ∂àÊÅØ
  const warning = (content: string, duration = 3000) => {
    message.warning(content, { duration })
  }

  // ÊôÆÈÄöÊ∂àÊÅØ
  const info = (content: string, duration = 3000) => {
    message.info(content, { duration })
  }

  // Âä†ËΩΩ‰∏≠Ê∂àÊÅØ
  const loading = (content: string) => {
    return message.loading(content, { duration: 0 })
  }

  // Á°ÆËÆ§ÂØπËØùÊ°Ü
  const confirm = (content: string, title = 'Á°ÆËÆ§'): Promise<boolean> => {
    return new Promise((resolve) => {
      dialog.warning({
        title,
        content,
        positiveText: 'Á°ÆÂÆö',
        negativeText: 'ÂèñÊ∂à',
        onPositiveClick: () => {
          resolve(true)
        },
        onNegativeClick: () => {
          resolve(false)
        },
        onClose: () => {
          resolve(false)
        }
      })
    })
  }

  // ÈÄöÁü•
  const notify = {
    success: (title: string, content?: string, duration = 4000) => {
      notification.success({
        title,
        content,
        duration
      })
    },
    error: (title: string, content?: string, duration = 4000) => {
      notification.error({
        title,
        content,
        duration
      })
    },
    warning: (title: string, content?: string, duration = 4000) => {
      notification.warning({
        title,
        content,
        duration
      })
    },
    info: (title: string, content?: string, duration = 4000) => {
      notification.info({
        title,
        content,
        duration
      })
    }
  }

  return {
    success,
    error,
    warning,
    info,
    loading,
    confirm,
    notify,
    loadingBar
  }
} 